import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Typography } from "@rp/react/web/mui/core"
import EmptyPlaceholder from "./assets/empty-watchlist.svg"

type Props = {
  placeholder: string
}

const WatchlistEmptyPlaceholder = ({ placeholder }: Props) => {
  const { isMobile } = useDeviceInfo()
  return (
    <Box
      sx={{
        width: 1,
        flexGrow: 1,
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        gap: 3,
      }}
    >
      <EmptyPlaceholder />
      <Typography align="center" maxWidth={isMobile ? 240 : 370} color="text.secondary">
        {placeholder}
      </Typography>
    </Box>
  )
}

export { WatchlistEmptyPlaceholder }
